// partners slider component

.partners {
    &_slider {
        &-slide {
            background: #fff;
            border: 1px solid $lightgray;
            min-height: 140px;
            display: flex;
            justify-content: center;
            align-items: center;

            img {
                max-height: 92px;
                width: auto;
            }
        }
    }
}
